import React, { useState } from 'react'
import { Carousel, CarouselProps } from '@mantine/carousel';
import MyCard from './MyCard.tsx';
import ScienceBox from './ScienceBox.tsx';
import '@mantine/carousel/styles.css';
import { useMediaQuery } from '@mantine/hooks';
import { useMantineTheme, Container, Paper, Indicator } from '@mantine/core';
import classesHoriz from './CarouselHoriz.module.css';
import classesVert from './CarouselVert.module.css';

export default function MyCarousel({windowSize}) {
  const theme = useMantineTheme();
  const mobile = useMediaQuery(`(max-width: ${theme.breakpoints.sm})`);

  const slideAspectRatio = 1.7  // Height / width of the card
  let landscape = true
  if (window.innerWidth < window.innerHeight) {
    landscape = false
  }

  // let height = 400
  let slideWidth = window.innerWidth * 0.85
  let slideHeight = slideWidth * slideAspectRatio
  let height = slideHeight

  let orientation: CarouselProps["orientation"] = 'horizontal'

  let myStyle, slideGap, classNames

  // console.log('mobile', mobile)

  if (landscape) {
    // If landscape then put scroll on the right
    height = window.innerHeight
    orientation = 'vertical'

    const maxSlideHeightRatio = 0.66
    const maxHeight = window.innerHeight * maxSlideHeightRatio
    const slideHeightRatio = slideHeight / maxHeight
    console.log('slideHeightRatio', slideHeightRatio)
    if (slideHeightRatio > 1) {
      slideWidth /= slideHeightRatio
      slideHeight /= slideHeightRatio
    }

    slideHeight = window.innerHeight * 0.9
    slideWidth = slideHeight / slideAspectRatio
    slideGap = slideHeight * 0.02

    // const carouselWidth = slideWidth
    myStyle = {
      position: 'fixed',
      right: "0px",
      // width: {carouselWidth},
      // minWidth: {carouselWidth},
      // maxWidth: {carouselWidth},
      height: {height},
      minHeight: {height},
      maxHeight: {height},
      top: '0px',
      padding: '0px 10px 0px 10px',
      backgroundColor: 'rgba(0, 0, 0, 0.3)',
    }
  } else {
    // If portrait (i.e. mobile) then put scroll on the bottom and ensure that the overall height of the slide won't be too large
    const maxSlideHeightRatio = 0.66
    const maxHeight = window.innerHeight * maxSlideHeightRatio
    const slideHeightRatio = slideHeight / maxHeight
    console.log('slideHeightRatio', slideHeightRatio)
    if (slideHeightRatio > 1) {
      slideWidth /= slideHeightRatio
      slideHeight /= slideHeightRatio
    }
    slideGap = slideWidth * 0.05
    orientation = 'horizontal'
    height = slideHeight
    myStyle = {
      position: 'fixed',
      // right: "0px",
      width: '100%',
      minWidth: '100%',
      maxWidth: '100%',
      bottom: `-${window.innerHeight}px`,
      justifyContent: "center",
      padding: '10px 0px 10px 0px',
      backgroundColor: 'rgba(0, 0, 0, 0.3)',
    }
    // classNames=classHoriz
  }




  console.log('MyCarousel', landscape, orientation, slideWidth, slideHeight)

  return (
    <div style={myStyle}>
      <Carousel
        withIndicators
        height={height}
        orientation={orientation}
        slideSize={{ base: `${slideWidth}` }}
        slideGap={slideGap}
        loop
        slidesToScroll={1}
        align='center'
        classNames={landscape ? classesVert : classesHoriz}
      >
        <Carousel.Slide>
          <ScienceBox number={1} width={slideWidth} height={slideHeight} />
        </Carousel.Slide>
        <Carousel.Slide>
          <ScienceBox number={2} width={slideWidth} height={slideHeight} />
        </Carousel.Slide>
        <Carousel.Slide>
          <ScienceBox number={3} width={slideWidth} height={slideHeight} />
        </Carousel.Slide>
        <Carousel.Slide>
          <ScienceBox number={4} width={slideWidth} height={slideHeight} />
        </Carousel.Slide>
        <Carousel.Slide>
          <ScienceBox number={5} width={slideWidth} height={slideHeight} />
        </Carousel.Slide>
        <Carousel.Slide>
          <ScienceBox number={6} width={slideWidth} height={slideHeight} />
        </Carousel.Slide>
        <Carousel.Slide>
          <ScienceBox number={7} width={slideWidth} height={slideHeight} />
        </Carousel.Slide>
        <Carousel.Slide>
          <ScienceBox number={8} width={slideWidth} height={slideHeight} />
        </Carousel.Slide>


      </Carousel>
    </div>
  );
}

// It would be good to hide the left/right buttons on mobile screens